 * @LastEditTime: 2020-09-10 14:10:12
 * @FilePath: \mrc-base-server-view\src\views\admin_account_manage\index.vue
--> 
<template>
  <div>
    <mrc_ess_table :headers='headers'
                    :hideAddBtn="!hasBtn('add-corporate-administrator')"
                   v-if="$route.name == 'admin_account_manage'"
                   pageName='admin_account_add'
                   :paginationOptions='paginationOptions'
                   @turnPage='getList'
                   :slotKey="['org_level_name','status']"
                   :isLoading='loading'
                   :items='items'>
      <template v-slot:top>
        <v-row dense>
          <v-col cols="12"
                 sm="4"
                 offset-sm="7">
            <!-- append-icon='mdi-magnify' -->
            <mrc_ess_searchInput prepend-icon='mdi-magnify'></mrc_ess_searchInput>
          </v-col>
        </v-row>
      </template>
      <template v-slot:item.org_level_name='{item}'>
        <div style="max-width: 500px;white-space: nowrap;;overflow: hidden;"
             class="d-flex align-center">
          <div style="max-width: 450px;white-space: nowrap;;overflow: hidden;">
            <v-chip v-for="(items,index) in item.org_level_name"
                    v-show="item.org_level_name.length > 0"
                    :key='index'
                    style="font-size: 12px;height:28px">{{items}}</v-chip>
          </div>
          <!-- <span>（others）</span> -->
        </div>
        <span v-show="item.org_level_name.length <= 0"></span>
      </template>
      <template v-slot:item.status='{item}'>
        <span class="status"
              :class="{'is_used': !item.status}">{{item.status ? $t('label_Working') : $t('label_Frozen')}}</span>
      </template>
      <template v-slot:item.actions='{item}'>
        <menu-list :list="[
                    { name: $t('label_View'),type: 'view', show: hasBtn('view-corporate-administrator')},
                    { name: $t('label_Edit'),type: 'edit', show: hasBtn('edit-corporate-administrator')},
                     {name: $t('label_To_reset_your_password'),type: 'resetPassword', show: hasBtn('reset-corporate-administrator-password') },
                     {name: $t('label_delete'),type: 'del', show: hasBtn('delete-corporate-administrator') }
                     ]"
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-corporate-administrator','edit-corporate-administrator','reset-corporate-administrator','delete-corporate-administrator']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { admin_account_list, admin_account_del } from '@/service/api/admin_account_manage'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      items: [],
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      },

    };
  },
  validations: {},
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_The_login_name'),
          value: 'user_account'
        },
        {
          text: this.$t('label_Associated_with_the_unit'),
          value: 'org_level_name',
          width: '500px'
        },
        {
          text: this.$t('label_state'),
          value: 'status'
        },
        {
          text: 'actions',
          value: 'actions'
        }
      ]
    }
  },
  created () { },
  watch: {
    async '$route' (val) {
      if (val.name == 'admin_account_manage') {
        await this.getList(this.paginationOptions)
        this.$store.commit('setTab', 0)
      }
    }
  },
  async mounted () {
    // await this.getBtnPermissionList()
    await this.getList(this.paginationOptions)
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    /**
 * @description: 点击menulist的事件
 * @param {type} 
 * @return: 
 * @author: lwq
 */
    async handleClick ({ item, index }, obj) {

      if (item.type == 'view') {
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_subPage_name', obj.user_account)

        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'admin_account_detail',
          params: {
            uuid: obj.uuid,
            name: obj.user_account
          }
        })
      }
      if (item.type == 'edit') {
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_subPage_name', obj.user_account)
        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'admin_account_detail',
          params: {
            uuid: obj.uuid,
            name: obj.user_account
          }
        })
      }
      if (item.type == 'del') {
        this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') })
          .then(async () => {
            const del = await admin_account_del({ uuid: obj.uuid })
            if (del.code == 200) {
              this.$Message({ text: this.$t('message_The_deletion_was_successful') })
              if (this.paginationOptions.cpage > 1 && this.items.length == 1) {
                this.paginationOptions.cpage--
              }
              await this.getList(this.paginationOptions)
            }
          }).catch(err => { })
      }
      if (item.type == 'resetPassword') {
        const reset = await mrc_admin_reset({ uuid: obj.uuid })
        if (reset.code == 200) {
          this.$Message({ text: this.$t('message_The_operation_was_successful'), })
          await this.getList(this.paginationOptions)
        }
      }

    },
    async getList (obj) {
      this.loading = true
      const { cpage, pagesize } = obj
      this.paginationOptions = obj
      const res = await admin_account_list({ cpage, pagesize, org_level_uuid: this.$store.state.currentCompany })
      if (res.code = 200) {
        this.items = res.data.map(item => {
          if (item.org_level_name)
            item.org_level_name = item.org_level_name.indexOf(',') > -1 ? item.org_level_name.split(',') : [item.org_level_name]
          else
            item.org_level_name = []
          return item
        })
        this.paginationOptions.totalCount = res.totalCount
        this.paginationOptions.totalpage = res.totalpage
      }
      this.loading = false
    }
  },
};
</script>

<style scoped>
</style>
